<template>
  <UPage>
    <UPageHeader 
      :title="`Category: ${categoryName}`" 
      :description="`Browse all content in the ${categoryName} category`" 
    />
    
    <UPageBody>
      <UPageGrid>
        <UPageCard
          v-for="item in categoryContent"
          :key="item._path"
          :to="item._path"
          :title="item.title"
          :description="item.excerpt"
          :ui="{ base: 'hover:shadow-lg transition-shadow' }"
        />
      </UPageGrid>
    </UPageBody>
  </UPage>
</template>

<script setup lang="ts">
const route = useRoute()
const categoryName = route.params.slug as string

const { data: categoryContent } = await useAsyncData(`category-${categoryName}`, () =>
  queryCollection('shop')
    .where('category', { $icontains: categoryName.replace('-', ' ') })
    .order('publishDate', 'DESC')
    .all()
)

if (!categoryContent.value?.length) {
  throw createError({
    statusCode: 404,
    statusMessage: 'Category not found'
  })
}
</script>